    font-size: 18px;
  }
}
.container {
  box-sizing: border-box;
  margin: 0 auto;
  min-width: 320px;
  max-width: 960px;
  padding-left: 20px;
  padding-right: 20px;
}
@media (min-width: 960px) {
  .container {
    padding-left: 10px;
    padding-right: 10px;
  }
}
.slider-controls {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 12px;
}
@media (min-width: 960px) {
  .slider-controls {
    display: none;
  }
}
.slider-controls__button {
  box-sizing: content-box;
  margin-left: 9px;
  margin-right: 10px;
  padding: 0;
  width: 6px;
  height: 6px;
  border: 0;
  border-radius: 50%;
  font-style: inherit;
  background-color: rgba(29, 38, 49, 0.3);
  cursor: pointer;
}
.slider-controls__button:not(.slider-controls__button--current):hover {
  background-color: rgba(29, 38, 49, 0.6);
}
.slider-controls__button:not(.slider-controls__button--current):active {
  background-color: rgba(29, 38, 49, 0.1);
}
.slider-controls__button--current {
  box-shadow: 0 0 0 3px #1D2631;
  background-color: transparent;
  cursor: default;
}
.slider-nav {
  display: none;
  justify-content: space-between;
}
@media (min-width: 960px) {
  .slider-nav {
    display: flex;
  }
}
.slider-nav__slider-button {
  position: relative;
  display: inline-block;
  vertical-align: middle;
  box-sizing: border-box;
  padding: 0;
  width: 22px;
  height: 41px;
  border: 0;
  font-style: inherit;
  background-color: transparent;
  cursor: pointer;
}
.slider-nav__slider-button::before {
  content: "";
  position: absolute;
  top: 70%;
  width: 27px;
  height: 27px;
  border-bottom: none;
  border-left: none;
  border-top: 2px solid rgba(29, 38, 49, 0.3);
  border-right: 2px solid rgba(29, 38, 49, 0.3);
}
.slider-nav__slider-button:hover::before {
  border-top-color: rgba(29, 38, 49, 0.6);
  border-right-color: rgba(29, 38, 49, 0.6);
}
.slider-nav__slider-button:active::before {
  border-top-color: rgba(29, 38, 49, 0.1);
  border-right-color: rgba(29, 38, 49, 0.1);
}
.slider-nav__slider-button--prev::before {
  left: 7px;
  transform: translateY(-50%) rotate(225deg);
}
.slider-nav__slider-button--next::before {
  right: 7px;
  transform: translateY(-50%) rotate(45deg);
}
.table {
  width: 834px;
  border-spacing: 0;
}
@media (min-width: 660px) {
  .table {
    width: 100%;
  }
}
.table__title {
  position: relative;
  padding-top: 13px;
  padding-bottom: 29px;
  border-right: 2px solid #e5e5e5;
  font-size: 18px;
  color: #ffffff;
  background-color: #283645;
}
@media (min-width: 660px) {
  .table__title {
    padding-top: 12px;
    padding-bottom: 10px;
    padding-left: 4px;
  }
}
@media (min-width: 960px) {
  .table__title {
    padding-top: 16px;
    padding-bottom: 21px;
  }
}
.table__title:first-child {
  display: none;
  border-right: 0;
  border-bottom: 2px solid #e5e5e5;
  background-color: transparent;
}
@media (min-width: 660px) {
  .table__title:first-child {
    display: table-cell;
  }
}
.table__title:last-child {
  border-right-color: #1D2631;
}
.table__title--hit {
  position: relative;
  background-image: url("../img/bg-hit.svg");
  background-position: 0 0;
  background-size: 74px;
  background-repeat: no-repeat;
}
@media (min-width: 660px) {
  .table__title--hit {
    background-image: url("../img/bg-hit-tablet.svg");
    background-size: 61px;
  }
}
@media (min-width: 960px) {
  .table__title--hit {
    background-image: url("../img/bg-hit.svg");
    background-size: 74px;
  }
}
.table__title--hit::before {
  content: attr(data-hit-text);
  position: absolute;
  top: 12px;
  left: 10px;
  transform: rotate(-45deg);
  font-size: 14px;
  font-weight: 400;
  color: #ffffff;
}
@media (min-width: 660px) {
  .table__title--hit::before {
    top: 3px;
    left: 7px;
  }
}
@media (min-width: 960px) {
  .table__title--hit::before {
    top: 12px;
    left: 12px;
  }
}
.table__type,
.table__cost {
  display: block;
}
.table__type {
  margin-bottom: 1px;
  font-weight: 700;
  font-size: 16px;
  line-height: 30px;
}
@media (min-width: 660px) {
  .table__type {
    margin-bottom: 2px;
  }
}
@media (min-width: 960px) {
  .table__type {
    font-size: 18px;
    margin-bottom: 1px;
  }
}
.table__cost {
  display: block;
  margin-top: -5px;
  font-size: 18px;
  line-height: 30px;
  font-weight: 300;
}
@media (min-width: 660px) {
  .table__cost {
    font-size: 18px;
  }
}
@media (min-width: 960px) {
  .table__cost {
    margin-top: 0;
    font-size: 24px;
  }
}
.table__kit {
  padding: 15px 17px 14px 22px;
  border-right: 2px solid #e5e5e5;
  border-bottom: 2px solid #e5e5e5;
  background-color: #ffffff;
}
@media (min-width: 660px) {
  .table__kit {
    padding: 7px 20px 8px;
    width: 25%;
  }
}
@media (min-width: 960px) {
  .table__kit {
    padding: 14px 20px 15px;
  }
}
.table__kit:first-child {
  display: none;
  border-left: 2px solid #e5e5e5;
}
@media (min-width: 660px) {
  .table__kit:first-child {
    display: table-cell;
    padding-left: 15px;
    padding-right: 14px;
    text-transform: none;
  }
}
@media (min-width: 960px) {
  .table__kit:first-child {
    padding-left: 21px;
    padding-right: 21px;
    font-size: 18px;
  }
}
.table__feature {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
@media (min-width: 660px) {
  .table__feature {
    justify-content: center;
  }
}
.table__feature::before {
  content: attr(data-feature-name);
}
@media (min-width: 660px) {
  .table__feature::before {
    content: "";
  }
}
.table__feature-value {
  display: block;
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
}
.table__feature-value--true {
  width: 31px;
  height: 21px;
  background-image: url("../img/icon-yes.svg");
}
.table__feature-value--false {
  width: 27px;
  height: 27px;
  background-image: url("../img/icon-no.svg");
}
.map__contacts {
  position: relative;
  overflow: hidden;
  height: 200px;
  background-image: url("../img/content/map-mobile.jpg");
  background-color: #f2f2f2;
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  min-height: 200px;
}
@media (min-resolution: 2dppx) {
  .map__contacts {
    background-image: url("../img/content/map-mobile@2x.jpg");
  }
}
@media (min-width: 660px) {
  .map__contacts {
    min-height: 374px;
    background-image: url("../img/content/map-tablet.jpg");
  }
}
@media (min-width: 660px) and (min-resolution: 2dppx) {
  .map__contacts {
    background-image: url("../img/content/map-tablet@2x.jpg");
  }
}
@media (min-width: 960px) {
  .map__contacts {
    min-height: 502px;
    background-image: url("../img/content/map-desktop.jpg");
  }
}
@media (min-width: 960px) and (min-resolution: 2dppx) {
  .map__contacts {
    background-image: url("../img/content/map-desktop@2x.jpg");
  }
}
.map__contacts::before {
  content: "";
  position: absolute;
  z-index: 100;
  top: 0;
  left: 0;
  right: 0;
  height: 33px;
  background-image: url("../img/bg-triangle-white-footer.svg");
  background-size: 100% 33px;
  background-position: 50% 0;
  background-repeat: no-repeat;
}
@media (min-width: 660px) {
  .map__contacts::before {
    height: 67px;
    background-size: 100% 67px;
  }
}
@media (min-width: 960px) {
  .map__contacts::before {
    height: 104px;
    background-size: 100% 104px;
  }
}
.map__contacts iframe {
  width: 100%;
  height: 200px;
  border: none;
}
@media (min-width: 660px) {
  .map__contacts iframe {
    height: 374px;
  }
}
@media (min-width: 960px) {
  .map__contacts iframe {
    height: 502px;
  }
}
.header-image img {
  display: block;
}
@media (min-width: 660px) {
  .header-image img {
    position: absolute;
    bottom: 0;
    right: 0;
  }
}
@media (min-width: 960px) {
  .header-image img {
    right: 4px;
  }
}
.mobile-hidden {
  display: none;
}
@media (min-width: 660px) {
  .mobile-hidden {
    display: inline;
  }
}
@media (min-width: 660px) {
  .tablet-hidden {
    display: none;
  }
}
@media (min-width: 960px) {
  .tablet-hidden {
    display: inline;
  }
}
